.App {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  background-color: white;
  transition: 0.782234s;
}

.App.isHover {
  background-color: black;
  transition: 0.782234s;
}

.click {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300%;
  width: 100%;
  background-color: aqua;
}
