.header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 15%;
  border: solid 1px black;
  background-color: wheat;
  transition: 1s;
}

.header.unclicked {
  width: 0%;
  height: 0%;
  border: none;
  transition: 1s;
}

.linkContainer {
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: solid 1px black;
  width: 40%;
  margin: 2% auto 0 auto;
}

.linkContainer.unclicked {
  display: none;
}
