.control {
  width: 40vw;
  height: 25vh;
  color: brown;
  /* border: solid 1px black; */

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.bottomContainer {
  /* border: solid 1px black; */
  width: 50%;
  margin: 0 auto 0 auto;
  height: 20%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.textBlock {
  /* border: solid 1px black; */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.textBlock > h1 {
  color: green;
  font-size: 60px;
  margin: 0;
}

.textBlock > h3 {
  margin: 0;
  font-size: 30px;
}
